@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
body {
  font-family:
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    'Roboto',
    'Oxygen',
    'Ubuntu',
    'Cantarell',
    'Fira Sans',
    'Droid Sans',
    'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.colorRed {
  color: red;
}

.colorBlue {
  color: blue;
}

.dNone {
  display: none;
}

.dFlex {
  display: -webkit-flex;
  display: flex;
}

.stock {
  font: normal normal 400 1rem/1rem 'Open Sans', sans-serif;
}

.stock:hover {
  background: lightgray;
  cursor: pointer;
}

.stock .price,
.stock .change {
  text-align: right;
}

.filter .content {
  margin: 1rem;
  font: normal normal 700 0.8rem/0.8rem 'Open Sans', sans-serif;
}

.filter .content select {
  font: normal normal 700 0.8rem/0.8rem 'Open Sans', sans-serif;
}

.stocks table {
  border-collapse: collapse;
}

th,
td {
  border: 1px solid lightgray;
  padding: 0.3rem 0.5rem;
}

button {
  background: white;
  border: none;
}

.index button {
  margin: 1rem;
  border: 1px solid lightgray;
  outline: none;
  text-shadow: 0 0 1px #000;
  box-shadow: 0 0 10px lightgray;
  font: normal normal 600 1rem/2rem 'Open Sans', sans-serif;
  border-radius: 1rem;
  padding: 0.5rem;
}

button:hover {
  cursor: pointer;
  background: lightgray;
}

.indexes {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.profile {
  font-family: 'Open Sans', sans-serif;
}

.profile .content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.profile .top {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.profile .top .logo {
  width: 64px;
}

.profile .top h2,
.profile .top h4 {
  margin-left: 2rem;
}

.profile .top h5 {
  margin-left: 0.25rem;
}

.profile .info {
  width: 75%;
}

.priceData {
  width: 60%;
}

.profile .row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.profile .cell {
  width: 100%;
  margin: 0.5rem 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  border-bottom: 1px solid lightgray;
}

.profile .info .cell {
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  border: none;
  font-weight: 700;
}

